.create-in-parsing {

    //.create-in-parsing__application-wrap{}
    &__application-wrap {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        overflow: hidden;
    }


    //.create-in-parsing__menu-items-container{}
    &__menu-items-container {
        display: flex;
        justify-content: space-between;
        color: #000;
    }

    // .create-edit-parser-list{
    // .create-in-parsing__menu-items-brand{}
    &__menu-items-brand {
        padding: 0;
        font-weight: 900;
        line-height: 120%;
        letter-spacing: 2px;
        color: #000000;
        font-family: 'Univia Pro';
        font-size: 11px;
        white-space: nowrap;
        // @media (min-width: 420px) {
        //     font-size: 16px;        
        // }    
    }
    // .create-in-parsing__menu-items-model{}
    &__menu-items-model {
        padding: 0;
        font-weight: 900;
        line-height: 120%;
        letter-spacing: 2px;
        color: #000000;
        font-family: 'Univia Pro';
        font-size: 11px;
        white-space: nowrap;
        // @media (min-width: 420px) {
        //     font-size: 16px;        
        // }    
    }

    //.create-in-parsing__menu-items-year{}
    &__menu-items-year {
        padding: 0;
        font-weight: 900;
        line-height: 120%;
        letter-spacing: 2px;
        color: #000000;
        font-family: 'Univia Pro';
        font-size: 11px;
        white-space: nowrap;
        // transform: translate(0px, -15px);  
        // @media (min-width: 420px) {
        //     font-size: 16px;        
        // } 
    }

    // }
    //.create-in-parsing__menu-items-text{}
    &__menu-items-text {
        flex: 1 1 0;
        padding: 0;
        font-family: 'Univia Pro';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.005em;
        font-feature-settings: 'tnum' on, 'lnum' on, 'kern' off;
        color: #132B45;
        max-height: 50px;
        min-height: 40px;
        text-align: start;
        overflow-y: auto;
    }

    //.create-in-parsing__slider-image{}
    &__slider-image {
        // max-width: 150px;
        flex: 70px 0;
        position: relative;
        display: block;
        transform: translate(0px, -20px);
    }


    //.create-in-parsing__application-container-list{}
    &__application-container-list {
        width: 100%;
        // margin-bottom: 75px;
    }

    //.create-in-parsing__application-items{}
    &__application-items {
        padding: 0;
        margin: 7px auto 8px;
        padding: 15px;
        position: relative;
        z-index: 11;
        background: rgba(255, 255, 255, 0.8);
        box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08), 0px 24px 48px 6px rgba(97, 97, 97, 0.08);

        border-radius: 8px;
        min-width: 300px;
        max-height: 120px;
        overflow: hidden; 
        min-height: 140px;
    }

    //.create-in-parsing__menu-items{}
    &__menu-items {
        position: relative;

    }

    //.create-in-parsing__menu-main-container-list{}
    &__menu-main-container-list {
        padding: 0;
        width: 90%;
        display: flex;
        justify-content: center;
        margin-bottom: 50px;
        position: relative;

        // margin: 0 auto 25px;
        // min-height: calc(100vh - 200px);
        // overflow: scroll;
    }

    //.create-in-parsing__menu-container-list{}
    &__menu-container-list {
        display: grid;
        grid-template-columns: 130px 130px;
        grid-auto-rows: minmax(70px, 100px);
        gap: 20px;
        position: relative;
        z-index: 11;
    }

    //.create-in-parsing__menu-title{}
    &__menu-title {
        padding: 0;
        position: relative;
        z-index: 11;
        margin: 20px 0;
        font-size: 20px;
        font-weight: 900;
        font-family: 'Univia Pro';
    }

    //.create-in-parsing__menu-description{}
    &__menu-description {
        margin: 0px auto 25px;
        padding: 15px;
        position: relative;
        z-index: 11;
        background: rgba(255, 255, 255, 0.8);
        box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08), 0px 24px 48px 6px rgba(97, 97, 97, 0.08);

        border-radius: 8px;
        min-width: 300px;
        width: 90%;

        &>div {
            float: left;
        }

    }

    //.create-in-parsing__menu-items{}
    &__menu-items {
        position: relative;
        background: rgba(255, 255, 255, 0.8);
        box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08), 0px 24px 48px 6px rgba(97, 97, 97, 0.08);

        border-radius: 8px;

        //.create-in-parsing__menu-items-title{}
        &-title {
            padding: 0;
        }

        //.create-in-parsing__menu-items-image{}
        &-image {
            margin: 20px;
            height: 20%;
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            position: relative;

            &::after {
                content: "";
                position: absolute;
                width: 50px;
                height: 50px;
                background-color: rgb(19 43 69 / 18%);
                transform: translate(20px, -16px);
                border-radius: 50%;
            }
        }
    }

    //.create-in-parsing__application-update-items{}
    &__application-update-items {
        float: right;
        margin-bottom: 13px;
        padding: 15px 20px 10px;
        background-color: rgba(255, 255, 255, 0.9);
        top: -10px;
        border-radius: 0 0 12px 12px;
        min-width: 120px;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 14px;
    }
}