.toggle{
    //.toggle__container{}
    &__container{
        // min-width: 320px;
        // max-width: 320px;
        min-height: 32px;
        max-height: 32px;
        background: var(--color-fone-background);
        border-radius: 8px;
        margin: 0 auto;
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        width: 100%;
        border: 0.5px solid var(--border-color);
    }
    &__item{
        //.toggle__item--active{}
        display: flex;
        align-items: center;
        justify-content: center;
        color: var(--text-color);
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 13px;
        line-height: 18px;
        &--active{
            width: 100%;
            height: 28px;
            background-color:var(--bgc-element-fone);
            border: 0.5px solid var(--border-color);
            box-shadow: 0px 4px 14px var(--box-shadow);
            border-radius: 8px;
            color: var(--red-color);
            margin-left: 1px;
            margin-right: 1px;
            pointer-events: none;
        }
    }
}