.message {

    //.message__description{}
    &__description {    
        margin-top: 16px;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 13px;
        line-height: 18px;
        width: 100%;
        max-width: 320px;
        padding-left: 5px;
        color: var(--placeholder);
        &>div {
            float: left;
        }
    }
}

.form-data-user {

    //.form-data-user__container{}
    &__container {
        padding: 0;
    }

    //.form-data-user__items{}
    &__items {
        padding: 0;
        display: flex;
    }

    //.form-data-user__items-title{}
    &__items-title {
        color: var(--placeholder);
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 13px;
        line-height: 18px;
        padding-right: 5px;
    }

    //.form-data-user__items-data{}
    &__items-data {
        padding: 0;
        
    }
}