.card-filter{
    //.card-filter__container{}
    &__container{
        padding: 0;
        // width: calc(100% - 30px);
        padding: 5px 0;
        &>div{
            &:last-child{
                margin-bottom: 50px;
            }
        }
    }
    //.card-filter__item{}
    &__item{
        padding: 0;
        pointer-events: all;
        padding: 0 15px;
        position: relative;
        border: 1px solid var(--border-color);
        border-radius: 8px;
        display: flex;
        align-items: center;
        min-height: 70px;
        // opacity: 0;
        // right: -100vw;
        transition: right .8s ease;
        border-radius: .6rem;
        margin-bottom: 4px;
        background-color: var(--bgc-element-fone);
        &> div {
            width:50px;
        }
    }
    //.card-filter__wrap-title{}
    &__wrap-title{
        display: flex;
        width: 100%;
        padding: 15px 0;
    }
    //.card-filter__icon-list{}
    &__icon-list{
        padding: 0;
        margin-left: 10px !important;
        margin-right: 15px !important;
    }
    //.card-filter__icon-child{}
    &__icon-child{
        position: absolute;
        right: 20px;
    }

    //.card-filter__name{}
    &__name{
        padding: 2px;
        width: 100%;
        color: var(--text-color);
    }
    //.card-filter__item-label{}
    &__item-label{
        color: var(--text-color);
    }
    //.card-filter__name-model{}
    &__name-model{
        padding: 0;
        display: flex;
        flex-wrap: wrap;
    }
    //.card-filter__name-generation{}
    &__name-generation{
        position: relative;
        margin-left: 15px;
        &::after{
            position: absolute;
            content: '';
            height: 100%;
            width: 1px;
            left: -7px;
            background-color: var(--border-color);

        }
    }
}