
.info{
    //.info__info-container-list{}
    &__info-container-list{
        padding: 0;
        width: 100%;
        // margin: 0 auto 25px;
        // overflow: scroll;
    }
    //.info__container-list{}
    &__container-list{
        width: 100%;
    }
    //.info__title{}
    &__title{
        padding: 0;
        position: relative;
        z-index: 11;
        margin: 20px 0;
        font-size: 20px;
        font-weight: 900;
        font-family: 'Univia Pro';
    }
    //.info__description{}
    &__description{

    }
    //.info__items{}
    &__items{
        padding: 20px;
        gap: 16px;        
        // width: 320px;
        height: 53.33px;
        background: var(--bgc-element-fone);
        box-shadow: 0px 4px 14px var(--box-shadow);
        border-radius: 14px;
        margin-bottom: 16px;
        display: flex;
        //.info__items-line{}
        &-line{
            width: 100%;
            height: 2px;
            margin-bottom: 16px;
            background-color: var(--border-color);
        }
        //.info__items-title{}
        &-title{
            padding: 0;
            text-align: center;
        }
        //.info__items-text{}
        &-text{
            text-align: center;
            font-family: 'Univia Pro';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 17px;
            text-align: center;
            letter-spacing: 0.005em;
            font-feature-settings: 'tnum' on, 'lnum' on, 'kern' off;
            color: var(--text-color);
        }
        //.info__items-image{}
        &-image{
            margin: 20px;
            height: 20%;
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            position: relative;
            &::after{
                content: "";
                position: absolute;
                width: 50px;
                height: 50px;
                background-color: rgb(19 43 69 / 18%);
                transform: translate(20px, -16px);
                border-radius: 50%;
            }
        }
    }
}