.card-filter{
    //.card-filter__container{}
    &__container{
        padding: 0;
        // width: calc(100% - 30px);
        padding: 5px 0;
    }
    //.card-filter__item{}
    &__item{
        background: var(--bgc-element-fone);
        box-shadow: 0px 4px 14px var(--box-shadow);
        border-radius: 8px;
        pointer-events: all;
        padding: 0 15px;
        position: relative;
        display: flex;
        align-items: center;
        transition: right .8s ease;
        margin-bottom: 4px;
        min-height: 50px;
    }
    //.card-filter__wrap-title{}
    &__wrap-title{
        display: flex;
        width: 100%;
        padding: 15px 0;
    }
    //.card-filter__icon-list{}
    &__icon-list{
        padding: 0;
        margin-left: 10px !important;
        margin-right: 15px !important;
    }
    //.card-filter__icon-child{}
    &__icon-child{
        position: absolute;
        right: 20px;
    }

    //.card-filter__name{}
    &__name{
        padding: 2px;
        color: var(--text-color);
    }
}