.card-application-auto-duplicate {
    //.card-application-auto-duplicate__status-icon{}
    &__status-icon{
        position: absolute;
        right: 0;
        // top: 50%;
        // transform: translate(0, -50%);
        z-index: 9;
    }
    //.card-application-auto-duplicate__application-wrap{}
    &__application-wrap {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        overflow: hidden;
    }


    //.card-application-auto-duplicate__menu-items-container{}
    &__menu-items-container {
        display: flex;
        justify-content: space-between;
        color: #000;
    }

    // .create-edit-parser-list{
    // .card-application-auto-duplicate__menu-items-brand{}
    &__menu-items-brand {
        padding: 0;
        font-weight: 900;
        line-height: 120%;
        letter-spacing: 3px;
        color: #000000;
        // font-family: 'Univia Pro';
        font-size: 13px;
        white-space: nowrap;
        // @media (min-width: 420px) {
        //     font-size: 16px;        
        // }    
    }
    // .card-application-auto-duplicate__menu-items-model{}
    &__menu-items-model {
        padding: 0;
        font-weight: 900;
        line-height: 120%;
        letter-spacing: 2px;
        color: #000000;
        font-family: 'Univia Pro';
        font-size: 12px;
        white-space: nowrap;
        // @media (min-width: 420px) {
        //     font-size: 16px;        
        // }    
    }

    //.card-application-auto-duplicate__menu-items-year{}
    &__menu-items-year {
        font-size: 11px;
        font-size: 11px;
        position: relative;
        display: block;
        // transform: translate(0px, -15px);  
        // @media (min-width: 420px) {
        //     font-size: 16px;        
        // } 
    }
    //.card-application-auto-duplicate__menu-items-date-create{}
    &__menu-items-date-create {
        font-size: 11px;
        font-size: 11px;
        position: relative;
        display: block;
        // transform: translate(0px, -15px);  
        // @media (min-width: 420px) {
        //     font-size: 16px;        
        // } 
    }

    // }
    //.card-application-auto-duplicate__menu-items-text{}
    &__menu-items-text {
        flex: 1 1 0;
        padding: 0;
        font-family: 'Univia Pro';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.005em;
        font-feature-settings: 'tnum' on, 'lnum' on, 'kern' off;
        color: #132B45;
        max-height: 50px;
        min-height: 40px;
        overflow: hidden;
        text-align: start;
        overflow-y: auto;
    }

    //.card-application-auto-duplicate__slider-image{}
    &__slider-image {
        // max-width: 150px;
        flex: 70px 0;
        position: relative;
        display: block;
        transform: translate(0px, 2px);
        margin-top: 12px;
    }


    //.card-application-auto-duplicate__application-container-list{}
    &__application-container-list {
        width: 100%;
        display: grid;
        // margin-bottom: 75px;
    }

    //.card-application-auto-duplicate__application-items{}
    &__application-items {
        padding: 0;
        margin: 7px auto 8px;
        padding: 15px;
        position: relative;
        z-index: 11;
        background: rgba(255, 255, 255, 0.8);
        // box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08), 0px 24px 48px 6px rgba(97, 97, 97, 0.08);
        box-shadow: 5px 7px 4px #9c9292;
        border-radius: 8px 8px 0 8px;
        min-width: 300px;
        max-height: 120px;
        overflow: hidden;
        min-height: 140px;
    }

    //.card-application-auto-duplicate__menu-items{}
    &__menu-items {
        position: relative;

    }

    //.card-application-auto-duplicate__menu-main-container-list{}
    &__menu-main-container-list {
        padding: 0;
        width: 90%;
        display: flex;
        justify-content: center;
        margin-bottom: 50px;
        position: relative;
        max-width: 450px;
        // margin: 0 auto 25px;
        // min-height: calc(100vh - 200px);
        // overflow: scroll;
    }

    //.card-application-auto-duplicate__menu-container-list{}
    &__menu-container-list {
        display: grid;
        grid-template-columns: 130px 130px;
        grid-auto-rows: minmax(70px, 100px);
        gap: 20px;
        position: relative;
        z-index: 11;
    }

    //.card-application-auto-duplicate__menu-title{}
    &__menu-title {
        padding: 0;
        position: relative;
        z-index: 11;
        margin: 20px 0;
        font-size: 20px;
        font-weight: 900;
        font-family: 'Univia Pro';
    }

    //.card-application-auto-duplicate__menu-description{}
    &__menu-description {
        margin: 0px auto 25px;
        padding: 15px;
        position: relative;
        z-index: 11;
        background: rgba(255, 255, 255, 0.8);
        box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08), 0px 24px 48px 6px rgba(97, 97, 97, 0.08);

        border-radius: 8px;
        min-width: 300px;
        width: 90%;

        &>div {
            float: left;
        }

    }

    //.card-application-auto-duplicate__menu-items{}
    &__menu-items {
        position: relative;
        background: rgba(255, 255, 255, 0.8);
        box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08), 0px 24px 48px 6px rgba(97, 97, 97, 0.08);

        border-radius: 8px;

        //.card-application-auto-duplicate__menu-items-title{}
        &-title {
            padding: 0;
        }

        //.card-application-auto-duplicate__menu-items-image{}
        &-image {
            margin: 20px;
            height: 20%;
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            position: relative;

            &::after {
                content: "";
                position: absolute;
                width: 50px;
                height: 50px;
                background-color: rgb(19 43 69 / 18%);
                transform: translate(20px, -16px);
                border-radius: 50%;
            }
        }
    }

    //.card-application-auto-duplicate__application-update-items{}
    &__application-update-items {
        float: right;
        margin-bottom: 13px;
        padding: 15px 20px 10px;
        background-color:  #c1c1c1;;
        top: -8px;
        border-radius: 0 0 12px 12px;
        min-width: 120px;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 14px;
        box-shadow: 5px 7px 4px #9c9292;
        z-index: 12;
        position: relative;
    }
}

.card-app{
    //.card-app__container{}
    &__container{
        padding: 0;
        margin-top: 9px;
        position: relative;
        z-index: 10;
    }
    //.card-app__info-block{}
    &__info-block{
        display: flex;
        justify-content: space-between;
        width: 100%;
    }
    //.card-app__where-app{}
    &__where-app{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 13px;
        line-height: 18px;
        display: flex;
        align-items: center;    
        color: var(--text-color);        
    }
    //.card-app__container-duplicat{}
    &__container-duplicat{
        display: flex;   
    }
    //.card-app__duplicat-btn{}
    &__duplicat-btn{
        color: #6987D4;    
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 22px;
        letter-spacing: -0.408px;
        font-feature-settings: 'case' on;
        margin-right: 4px;
    }
    //.card-app__data-card-block{}
    &__data-card-block{
        min-width: 320px;
        min-height: 124px;        
        background: var(--bgc-element-fone);
        box-shadow: 0px 4px 14px var(--box-shadow);
        border-radius: 14px;
    }

    //.card-app__data-card-user{}
    &__data-card-user{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
        display: flex;
        align-items: center;
        letter-spacing: -0.5px;
        font-feature-settings: 'case' on;
        color: var(--text-color);    
        margin-bottom: 8px;    
    }
    //.card-app__data-card-auto{}
    &__data-card-auto{
        padding: 0;
        color: var(--text-color);      
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
        display: flex;
        align-items: center;
        letter-spacing: -0.5px;
        font-feature-settings: 'case' on;  
        margin-bottom: 8px;
    }
    //.card-app__data-card-auto-vertical-dash--color{}
    &__data-card-auto-vertical-dash--color{
        color: var(--text-color);
        opacity: .2;
        margin-left: 7px;
        margin-right: 7px;
    }
    //.card-app__data-card-description{}
    &__data-card-description{
        color: var(--text-color);      
        font-family: 'Inter';
        font-style: normal;
        font-weight: 400;
        font-size: 13px;
        line-height: 18px;
        display: flex;
        align-items: center;
        letter-spacing: -0.408px;
        font-feature-settings: 'case' on;
        position: relative;
        z-index: 11;
    }
    //.card-app__data-card-container-image{}
    &__data-card-container-image{
        margin-top: 16px;
        display: flex;
        flex-wrap: wrap;        
    }
    //.card-app__data-card-item-image{}
    &__data-card-item-image{
        min-width: 63px;
        min-height: 63px;
        max-width: 63px;
        max-height: 63px;
        border-radius: 8px;
        overflow: hidden;
        margin-right: 7px;
        margin-bottom: 7px;
        background-color: var(--color-fone-background);
    }
    //.
    //.

}