.see-review {
    //.see-review__input-search{}
    &__input-search{
        width: 90% !important;
        margin: 0 auto !important;
    }
    //.see-review__container{}
    &__container {
        width: 100%;
        z-index: 98;
        position: relative;
    }

    //.see-review__car-list-container{}
    &__car-list-container {
        padding: 0;
    }

    //.see-review__description{}
    &__description {
        min-height: 43px;
        margin: 0 15px 25px;
        // width: calc(100% - 30px);
        padding: 15px;
        position: relative;
        z-index: 11;
        background: rgba(255, 255, 255, 0.8);
        // box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08), 0px 24px 48px 6px rgba(97, 97, 97, 0.08);
        box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
        border-radius: 8px;
        min-width: 300px;

        &>div {
            float: left;
        }
    }

    //.see-review__menu-search{}
    &__menu-search {
        min-height: 43px;
        margin: 0 15px 25px;
        // width: calc(100% - 30px);
        padding: 15px;
        position: relative;
        background: rgba(255, 255, 255, 0.8);
        box-shadow: 0px 8px 16px 2px rgba(97, 97, 97, 0.1), 0px 16px 32px 2px rgba(97, 97, 97, 0.1);
        border-radius: 8px;
        min-width: 300px;

        &>span {
            z-index: 16px;
        }
    }
}

.create-edit-parser-list{
    //.create-edit-parser-list__container-application{}
    &__container-application{
        display: flex;
        padding: 15px 0;
    }
    //.create-edit-parser-list__slider-image{}
    &__slider-image{
        // max-width: 150px;
        flex: 100px 0;
    }
    //.create-edit-parser-list__menu-main-container-list{}
    &__menu-main-container-list{
        // width: 100vw;
    }
    //.create-edit-parser-list__menu-container-list{}
    &__menu-container-list{
        display: grid;
        grid-template-columns: auto;
        // grid-auto-rows: minmax(70px, 155px);
        gap: 0px;
        position: relative;
        z-index: 11;
    }
    //.create-edit-parser-list__menu-title{}
    &__menu-title{
        padding: 0;
        position: relative;
        z-index: 11;
        margin: 20px 0;
        font-size: 20px;
        font-weight: 900;
        font-family: 'Univia Pro';
    }
    //.create-edit-parser-list__menu-description{}
    &__menu-description{
        margin: 10px auto 25px;
        padding: 15px;
        position: relative;
        z-index: 11;
        background: rgba(255, 255, 255, 0.8);
        box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08), 0px 24px 48px 6px rgba(97, 97, 97, 0.08);
        border-radius: 8px;
        min-width: 300px;
        width: 90%;
        &>div{
            float: left;
        }
    }
    //.create-edit-parser-list__menu-items-review-data-create{}
    &__menu-items-review-data-create{
        padding: 0;
        width: 100%;
        text-align: end;
        position: relative;
        font-size: 10px;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 22px;
        display: flex;
        align-items: center;
        letter-spacing: -0.408px;
        font-feature-settings: 'case' on;
        color: var(--placeholder);
    }
    //.create-edit-parser-list__menu-items{}
    &__menu-items{
        z-index: 11;
        background-color: transparent;        
        margin-bottom: 8px;        
        position: relative;
        z-index: 10;
        &::after{
            content: '';
            background-color: var(--bgc-element-fone);
            width: 100vw;
            height: 100%;
            z-index: -1;
            position: absolute;
            left: var(--margin);
            top: 0;
        }
        
        //.create-edit-parser-list__menu-items-container-name{}
        &-container-name{
            width: 100%;
            display: flex;
            justify-content: space-between;
        }
        //.create-edit-parser-list__menu-items-name-user{}
        &-name-user{
            font-family: 'Inter';
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            display: flex;
            align-items: center;
            letter-spacing: -0.5px;
            font-feature-settings: 'case' on;
            color: var(--text-color);
            min-width: 220px;
        }
        //.create-edit-parser-list__menu-items-text{}
        &-text{
            font-family: 'Inter';
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 22px;
            display: flex;
            align-items: center;
            letter-spacing: -0.408px;
            font-feature-settings: 'case' on;
            color: var(--text-color);
        }
        //.create-edit-parser-list__menu-items-image{}
        &-image{
            margin: 20px;
            height: 20%;
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            position: relative;
            &::after{
                content: "";
                position: absolute;
                width: 70px;
                height: 70px;
                background-color: rgb(19 43 69 / 18%);
                transform: translate(0px, -27px);
                // border-radius: 50%;
            }
        }
        //.create-edit-parser-list__menu-items-title{}
        &-title{
            padding: 0;
        }
        //.create-edit-parser-list__menu-items-container{}
        &-container{
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;

        }       
        
    }
    //.create-edit-parser-list__status-icon{}
    &__status-icon{
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate(0, -50%);
        z-index: 9;
    }
}

.create-edit-parser-list{
    // .create-edit-parser-list__menu-items-model{}
    &__menu-items-model{
        padding: 0;
        font-weight: 900;
        line-height: 120%;
        letter-spacing: 2px;
        color: #0915fe;
        font-family: 'Univia Pro';
        font-size: 11px; 
        max-width: 200px;
        white-space: pre-line;
    }
    //.create-edit-parser-list__menu-items-review{}
    &__menu-items-review{
        font-size: 11px;      
        position: relative;

    }

}
