:root{
  --text-color: #282828;
  --bgc-element-fone: #ffffff;
  --color-fone-background: #f8f7f6;
  --placeholder: rgba(40,40,40,.6);
  --box-shadow: rgba(0,0,0,.05);
  --text-color-link: #6987D4;
  --border-color: #E4E4E4;
  --red-color: #E43A3E;
  --color-fone-icon: white;
  --swiper-color: #222327;
  --focus-margin: 0px;
 
  --max-width: 500px;
  --min-width: 360px;
  --bgc-burger-menu: #F5F1D4;//#132B45;
  --text-menu: #1C3440;
  --bgc-decoration--black: #132B45;
  --bgc-decoration--grey: #132b453a;
  --bgc-decoration--red: #8B162B;
  --bgc-decoration--white: #ffffff;
  --bgc-white: #ffffff;
  --color-text: #000;
  --height-info: 0px;

}
.icon-color{
  fill: #ff0000;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px var(--bgc-element-fone) inset !important;
    -webkit-text-fill-color: var(--text-color) !important;
}
//.#form-payment-access__discription-context-item-title-description-3{}
#form-payment-access__discription-context-item-title-description-3{
    color: var(--placeholder);
  }
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-fone-background);
}

#root{
  overflow: hidden;
  margin: 0 auto;
}

#image{
    width: 100%;
    height: 50px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@import './normalize.scss';
@import './fonts/fonts.scss';
@import './icons.scss';
@import './phone-number.scss';

select {
  background-color: var(--bg-native-select-color);
}

.container{
  // margin: 0 auto 60px;
  display: flex;
  flex-direction: column;
  // align-items: center;
  position: relative;
  width: 100%;
  // height: calc(100vh - var(--height-info));
  // max-width: var(--max-width);
  min-width: var(--min-width);
  overflow: hidden;
  // top: 60px;
}

.main-context{
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow-y: scroll;
  // padding-bottom: 70px;
  overflow-x: hidden;
  // min-height: calc(100% - 111px);
  position: relative;
  transition: top .8s ease-out 0s;
  background-color: var(--bgc-container-fone);
  margin-top: 50px;
  margin-bottom: 70px;
}

.goto{
  // min-height: 50px;
  position: absolute;
  top: 95px;
}
.iframe-test{
  min-height: 100%;
  height: 100vh;
}

.full-screen{
  width: 95% !important;
  height: 95% !important;
  display: block !important;
  &>div>div>div{
    width: 100%;
  }
}

.toogle-theme{
  width: 60px;
  min-height: 30px;
  display: flex;
  align-items: center;
  padding: 0 7px;
  position: absolute;
  left: -4px;
  bottom: 30px;
  transform: rotate(90deg);
  z-index: 99999999;
  transition: all .8 ease-in-out;
  &::after{
    position: absolute;
    left: 0;
    top: 0;
    background-color: #090909;
    content: '';
    height: 100%;
    width: 50%;
    border-radius: 50% 0 0 60%;
  } 
  &::before{
    position: absolute;
    right: 0;
    top: 0;
    background-color: var(--bgc-element-fone);
    content: '';
    height: 100%;
    width: 50%;
    z-index: 5;
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    border-right: 1px solid #000;
    border-radius: 0  50% 50% 0;
  } 
}

.lmMaya{
  border: none !important;
  border-radius: 8px;
  max-width: calc(100% - 15px);
  margin: 25px auto 0;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.swiper-button-next{
  position: fixed !important;
  transform: translate(-20px,-30px);
  &::after{
    z-index: 10;
    color: var(--swiper-color);
    transform: scale(.5);
  }
  &::before{
    position: absolute;
    z-index: 9;
    content: '';
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-fone-background);
    min-width: 42px;
    min-height: 34px;
    border-radius: 50%;
    transform: translate(-5px,0);
    border: 1px solid var(--border-color);
  } 
}
.swiper-button-prev{
  position: fixed !important;
  transform: translate(18px,-30px);
  &::after{
    z-index: 10;
    color: var(--swiper-color);
    transform: scale(.5);
  }
  &::before{
    position: absolute;
    z-index: 9;
    content: '';
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-fone-background);
    min-width: 42px;
    min-height: 34px;
    border-radius: 50%;
    transform: translate(-5px,0);
    border: 1px solid var(--border-color);
  } 
}
.swiper-pagination-bullet-active{
  background-color: var(--bgc-decoration--red) !important;
}
