.form-payment-access{
    //.form-payment-access__container{}
    &__container{
        // width: 100%;
        background: var(--bgc-element-fone);
        box-shadow: 0px 4px 14px var(--box-shadow);
        // margin: 0 auto 10px;
        // padding: 16px 20px;
        padding: 16px 0px;
    }
    //.form-payment-access__nav-container{}
    &__nav-container{
        display: grid;
        grid-template-columns: 1fr 1fr;

    }
    //.form-payment-access__nav-button{}
    &__nav-button{
        width: 100%;
        margin-top: 5px;
        padding: 5px 5px 5px 5px;
        border-radius: 7px;
        min-height: 56px;
        display: flex;
        align-items: center;    
        justify-content: center;
        text-align: center;
        color: #5B75B2;
        font-size: 14px;
        font-weight: 900;
        //.form-payment-access__nav-button--active{}
        &--active{
            background-color: #004CFF;
            color: #becbeb;
        }
    }
    //.form-payment-access__discription{}
    &__discription{
        margin-top: 20px;
        margin-bottom: 20px;
    }
    //.form-payment-access__discription-wrap{}
    &__discription-wrap{
        padding: 0;
    }
    //.form-payment-access__discription-title{}
    &__discription-title{
        color: var(--text-color);
        font-weight: 900;
        font-size: 16px;
        line-height: 120%;        
    }
    //.form-payment-access__discription-context-list{}
    &__discription-context-list{
        padding: 0;
        margin-top: 15px;
    }
    //.form-payment-access__discription-context-item{}
    &__discription-context-item{
        display: flex;
        align-items: baseline;
        &>div:nth-child(1){
            transform: translate(-4px, 5px);
        }
    }
    //.form-payment-access__discription-context-item-title{}
    &__discription-context-item-title{
        color: var(--text-color);
        font-weight: 900;
        font-size: 12px;
        margin-right: 20px;
        line-height: 240%;   
        min-width: fit-content;        
    }
    //.form-payment-access__discription-context-item-desc{}
    &__discription-context-item-desc{
        color: var(--placeholder);
        padding: 0;
        position: relative;
        &::before{
            content: '';
            width: 10px;
            height: 1px;
            position: absolute;
            background-color: var(--text-color);
            top: 8px;
            left: -13px;
        }   
    }
}
