//.input__wrapper{}
.input{
    &__wrapper{
        width: 100%;
        display: flex;
        justify-content: space-between;        
        position: relative;
        min-height: 48px;
        // margin-bottom: 15px;
        // min-width: 325px;
        background-color: var(--bgc-element-fone) !important;
        border: 1px solid var(--border-color) !important;
        box-shadow: 0px 4px 14px var(--box-shadow) !important;
        border-radius: 8px !important;
    }
    //.input__input{}
    &__input{
        color: var(--text-color) !important;
        font-size: 14px;
        width: 100%;
        letter-spacing: 1.1px;
        line-height: 140%;
        padding: 10px;
        background-color: var(--bgc-element-fone);
        border-radius: 8px;

        &::placeholder{
            color: var(--placeholder);
            font-size: 14px;
            letter-spacing: 1.1px;
            line-height: 140%;
        }
    }
    //.input__label{}
    &__label{
        font-size: 12px;
        color:  var(--text-color);
        margin: 0 0 0 10px;
        position: relative;
        bottom: 4px;
    }
    //.input__error{}
    &__error{
        position: relative;
        &>input{
            color: #ff0000;
        }
    }
}


[name=input-search]{
    width: 320px;
    background: transparent;
    margin: 5px;
    padding: 0 12px 0 8px;
    &::-webkit-input-placeholder {
        /* Edge */
        color:  #08080871;
        font-size: 14px;

    }
    &:-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color:  #08080871;
        font-size: 14px;
    }
    &::placeholder {
        color:  #08080871;
        font-size: 14px;
    }
}

.error-user{
    border: 1px solid var(--red-color) !important;
}