.detali-info{
    //.detali-info__container-block-info{}
    &__container-block-info{
        padding: 0;
    }
    //.detali-info__container
    &__container{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 22px;
        letter-spacing: -0.408px;
        font-feature-settings: 'case' on;
        color: var(--text-color);
        display: flex;
        flex-wrap: wrap;
    }
    //.detali-info__container-btn{}
    &__container-btn{
        // width: 320px;
        margin: 16px auto 0;
        height: 120px;
        background-color: var(--bgc-element-fone) ;
        box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.05);
        border-radius: 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        overflow: hidden;
        position: relative;
    }
    //.detali-info__play-container-image{}
    &__play-container-image{
        padding: 0;
        position: absolute;
        top: 30%;
        // left: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        filter: blur(5px);
    }
    //.detali-info__text-youtube{}
    &__text-youtube{
        padding: 0;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 17px;
        line-height: 22px;
        display: flex;
        align-items: center;
        letter-spacing: -0.408px;
        color: var(--text-color);
    }
    //.detali-info__play-image{}
    &__play-image{
        padding: 0;
        filter: blur(0px);
        transform: translate(0, 20px);
        position: relative;
        &::after{
            content: "";
            position: absolute;
            min-width: 40px;
            min-height: 40px;
            border-radius: 50%;
            border: 1px solid white;
            left: 42%;
            top: 50%;
            transform: translate(-50%, -50%);
        }

    }
}   