.autopayment{
    //.autopayment__container{}
    &__container{
        width: 100%;
        // min-height: 150px;
        background: var(--bgc-element-fone);
        box-shadow: 0px 4px 14px var(--box-shadow);
        border-radius: 14px;
        padding: 16px 20px 8px;
    }
    //.autopayment__check-container{}
    &__check-container{
        padding: 0px 0 16px;
        //.autopayment__check-container-status{}
        &-status{
            padding: 0;
        }
    }

    //.autopayment__check-container-action{}
    &__check-container-action{
        padding: 0;
        margin: 16px 20px 0; 
    }
    //.autopayment__check-action-btn-cancel{}
    &__check-action-btn-cancel{
        // min-width: 320px;
        min-height: 48px;
        width: 100%;
        color: var(--red-color);
        font-size: 15px;
        padding: 0px 5px;
        letter-spacing: 0.4px;
        border: 1px solid var(--red-color);
        border-radius: 8px;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
        letter-spacing: -0.5px;
        font-feature-settings: 'case' on;
    }
    //.autopayment__status-blue{}
    &__status-blue{
        color: rgb(85, 85, 220);
    }
     //.autopayment__status-green{}
     &__status-green{
        color: rgb(35, 143, 35);
     }
}