.preview-image{
    //.preview-image__container{}
    &__container{
        padding: 0;
        display: block;
        overflow: auto;
        width: 75vw;
        height: 50vh;
        display: flex;
        &>div>div{
            padding: 0;
            align-items: center;
            &>div{
            display: flex;
            // height: 100vh;
            justify-content: center;
            align-items: center;
        }}
    }
    //.preview-image__container-control-button{}
    &__container-control-button{
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: space-evenly;   
    }
    //.preview-image__slider-swiper-image{}
    &__slider-swiper-image{
        // overflow: auto;
        display: flex;
        justify-content: center;
    }
}