.messagepayment{
    //.messagepayment__description-wrap{}
    &__description-wrap{
        width: 100%;
        display: flex;
        justify-content: flex-start;
        min-height: fit-content;
    }
    //.messagepayment__description{}
    &__description{
        background: var(--bgc-element-fone);
        box-shadow: 0px 4px 14px var(--box-shadow);
        border-radius: 14px;
        width: 100%;
        padding: 16px 20px;
        &>div{
            &> div{
                display: flex;
                color: var(--text-color);
                flex-direction: column;
                font-family: 'Inter';
                font-style: normal;
                font-weight: 600;
                font-size: 15px;
                line-height: 20px;
                letter-spacing: -0.5px;
                font-feature-settings: 'case' on;
                margin-bottom: 8px;
                &:last-child{
                    flex-direction: row;

                }
            }
        }
    }
}