.pagination{
    //.pagination__container{}
    &__container{
        width: 100%;
        align-items: flex-end;
        justify-items: center;
        margin: 20px 0;
        max-width: 300px;
        display: flex;
        margin-top: 50px;
        // overflow: hidden;        
    }
    //.pagination__inner-container{}
    &__inner-container{
        display: grid;
        grid-auto-columns: 50px; 
        grid-auto-flow:column;
        grid-template-rows: 1fr;
        justify-content: center;
    }
    //.pagination__item{}
    &__item{
        margin-right: 15px;
        font-size: 14px;
        position: relative;
        width: 32px;
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: var(--text-color);
        //.pagination__item-active{}
        &-active{
            width: 32px;
            height: 32px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #ff0000;
            font-size: 16px;
            position: relative;
            cursor: not-allowed;
            background-color: var(--bgc-element-fone);
            // &::after{
            //     content: "";
            //     box-shadow: 0 0px 5px 3px var(--bgc-element-fone);
            //     border-radius: 2px;
            //     position: absolute;
            //     height: 35px;
            //     width: 35px;
            // }
        }

        &:hover{
            cursor: pointer;
            color: rgb(19, 13, 17);
        }
    }
}