

.create-edit-parser-list{
    //.create-edit-parser-list__line{}
    &__line{
        width: 100%;
        height: 1px;
        background-color: var(--box-shadow);
        margin-top: 16px;
        margin-bottom: 8px;
    }
    //.create-edit-parser-list__items{}
    &__items{
        padding: 0;
        margin-bottom: 10px;
    }
    //.create-edit-parser-list__container-application{}
    &__container-application{
        display: flex;
        overflow: hidden;
        padding: 15px;
        flex-direction: column;
        background-color: transparent;
        background-color: var(--bgc-element-fone);
        box-shadow: 0px 4px 14px var(--box-shadow);
        border-radius: 14px;
    }
    //.create-edit-parser-list__menu-main-container-list{}
    &__menu-main-container-list{
        padding: 0;
        width: 100%;
        // margin: 0 auto 25px;
        // min-height: calc(100vh - 200px);
        // overflow: scroll;
    }
    //.create-edit-parser-list__menu-container-list{}
    &__menu-container-list{
        display: grid;
        grid-template-columns: auto;
        grid-auto-rows: auto; //minmax(70px, 130px);
        gap: 0px;
        position: relative;
        z-index: 11;
    }
    //.create-edit-parser-list__wrap{}
    &__wrap{
        padding: 0;
        margin-bottom: 8px;
        
        position: relative;
        //.create-edit-parser-list__wrap--line{}
        &--line:after{
            top: -10px;
            content: '';
            width: 100%;
            height: 1px;
            box-shadow: 2px 2px 3px var(--border-color);
            position: absolute;
            left: 0;
        }
    }
   
    //.create-edit-parser-list__item{}
    &__item{
        display: flex;
        flex-direction: column;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 22px;
        color: var(--placeholder);
        & > span:last-child{
            font-family: 'Inter';
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            display: flex;
            align-items: center;
            letter-spacing: -0.5px;
            font-feature-settings: 'case' on;
            color: var(--text-color);
        }
        //.create-edit-parser-list__item-name{}
        &-name{
            text-transform: uppercase;
            font-family: 'Inter';
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            display: flex;
            align-items: center;
            letter-spacing: -0.5px;
            font-feature-settings: 'case' on;
            color: var(--text-color);
        }
        //.create-edit-parser-list__item--link{}
        &--link{
            display: flex;
            flex-direction: column;
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 13px;
            line-height: 22px;
            color: var(--placeholder);
            // & > :first-child{
                //     color: #000;
                // }
            }
            //.create-edit-parser-list__item--list{}
            &--list{
                display: flex;
                flex-direction: column;
                font-family: 'Inter';
                font-style: normal;
                font-weight: 500;
                font-size: 13px;
                line-height: 22px;
                color: var(--placeholder);
                & > div{
                    font-family: 'Inter';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 15px;
                    display: flex;
                    align-items: center;
                    letter-spacing: -0.5px;
                    font-feature-settings: 'case' on;
                    color: var(--text-color);
                    line-height: 20px;
                }
                //.create-edit-parser-list__item--list-item-item{}
                &-item{
                    padding: 0;
                    pointer-events: none;
                    &-item{
                        pointer-events: all;
                        font-family: 'Inter';
                        font-style: normal;
                        font-weight: 600;
                        font-size: 15px;
                        line-height: 20px;
                        display: flex;
                        align-items: center;
                        letter-spacing: -0.5px;
                        font-feature-settings: 'case' on;
                        color: #6987D4;
                    }
                }
            }
    }
    //.create-edit-parser-list__item-icon-info{}
    &__item-icon-info{
        position: absolute;
        right: 0;
        top: 0;
    }
}

.create-edit-parser-list{
    // .create-edit-parser-list__menu-items-model{}
    &__menu-items-model{
        padding: 0;
        font-weight: 900;
        line-height: 120%;
        letter-spacing: 2px;
        color: #0915fe;
        font-family: 'Univia Pro';
        font-size: 11px; 
        max-width: 200px;
        white-space: pre-line;
    }
    //.create-edit-parser-list__menu-items-review{}
    &__menu-items-review{
        font-size: 11px;      
        position: relative;
        top: -13px;  
        // @media (min-width: 420px) {
        //     font-size: 16px;        
        // } 
    }

}
