
.textarea{
    //.textarea__container{}
    &__container{
        padding: 0;
        min-height: 100px;
        display: flex;
        flex-direction: column;
        position: relative;
        width: calc(100%);
        margin: 0 auto;
        border-color: var(--border-color);
        border-width: 1px;
        border-style: solid;
    }
    //.textarea__label{}
    &__label{
        padding: 0;
        position: absolute;
        top: -15px;
        font-size: 13px;
        left: 10px;
        color: var(--text-color);
    }
    //.textarea__textarea{}
    &__textarea{
        padding: 11px;        
        position: relative;
        font-size: 14px;
        letter-spacing: 1.1px;
        line-height: 140%;
        width: calc(100% - 0px);
        margin: 0 auto;        
        &::placeholder{
            color: var(--border-color);
            font-size: 16px;
            letter-spacing: 1.1px;
            line-height: 140%;
        }
    }
    //.textarea__help-text{}
    &__help-text{
        padding: 0;
        position: absolute;
        top: 91%;
        right: 16px;
        background: var(--bgc-element-fone);
        color: var(--red-color);
    }
    //.textarea-application{}
    &-application{
        border-radius: 8px;
        overflow: hidden;
        margin-top: 16px;
        //.textarea-application--anketa{}
        &--anketa{
            overflow: hidden;
            margin-top: 0;
            border-radius: 8px;
        }
    }
}

.feedback__textarea{
    height: 88px;
}
