.raiting-star {

    //.raiting-star__container{}
    &__container {
        max-width: 150px;
        display: flex;
        // align-items: center;
        margin-bottom: 20px;
    }
    //.raiting-star__star{}
    &__star {
        display: flex;
        &:hover{
            cursor: pointer;
        }
        &--active{
            padding: 0;
        }
    }
    //.raiting-star__label{}
    &__label{
        padding: 0 10px 0 0;
        // display: ruby;
        white-space: pre;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
        color: var(--text-color);
        letter-spacing: -0.5px;
        font-feature-settings: 'case' on;
        display: flex;
    }

}